.iniciar {
    background:    #ffff00;
    border:        2px solid #556699;
    border-radius: 5px;
    padding:       8px 20px;
    color:         #000000;
    display:       inline-block;
    font:          normal bold 27px/1 "Open Sans", sans-serif;
    text-align:    center;
 }

 .botao{
     background-color: #000000;
     margin-top: 30%;
     margin-left: 2%;
     width: 200px;
     height: 80px;
     align-items: center;
     display: flex;
     flex-direction: column;
 }

 .botao a{
     color: #ffff;
     padding: 8px 18px 8px;
     text-decoration: none;
     font-size: 15px;
     align-items: center;
 }
