*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.img{
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: 2%;
    margin-top: 5%;
    background-image: url(texto.png);
    width: 45%;
    height: 127px;
}

body{
    background-color: #ffffff;


}

.flexbox{
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    margin: 0 auto;
    padding: 15px;
}

.flexbox .board{
    flex-direction: row;
    margin-top: 8%;
    margin-right: 1%;
    display:flex;
    width: 45%; 
    height: 450px;
    background-color: transparent;
    padding: 2%;
    border-width: 3px;
    border-style: dashed;
    border-top-color: #000000;

}

#card-1{
    background-size: 100%;
    margin-left: 2%;
    background-repeat: no-repeat;
    position: absolute;
    margin-top: 18%;
    background-image: url(peixe.png);
    padding: 15px;
    width: 13%;
    height: 22%;
    cursor: pointer;
    margin-bottom: 15px;
    background-color: transparent;

}

