.botao{
    background-color: #000000;
    margin-top: 0%;
    margin-left: 70%;
    width: 200px;
    height: 60px;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.botao a{
    color: #ffff;
    padding: 8px 18px 8px;
    text-decoration: none;
    font-size: 15px;
    align-items: center;
}
